:root {
  --main-color: #00acdc;
  --text-accent-color: #060b35;
  --text-secondary-color: #616070;
}

* {
  box-sizing: border-box;
}

html {
  color: var(--text-secondary-color);
  font-family: Roboto, sans-serif;
  font-size: 10px;
  line-height: 14px;
}

body {
  margin: 0;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: var(--text-accent-color);
  margin: 0 auto 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: var(--text-secondary-color);
  margin: 0 auto 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rate {
  justify-content: space-between;
  align-items: flex-end;
  margin: 0 auto 24px;
  display: flex;
}

.card__reviews-count {
  color: var(--text-accent-color);
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin: 0 auto 16px;
  display: flex;
}

.card__price-label {
  color: var(--text-secondary-color);
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: var(--text-accent-color);
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  border: 1px solid var(--main-color);
  color: #fff;
  text-transform: uppercase;
  background-color: var(--main-color);
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: var(--main-color);
  background-color: #fff;
}

.stars {
  display: flex;
}

